  .adobeillu:before {
    content: attr(data-hover);
    font-size: large;
    /* font-family: Arial, Helvetica, sans-serif; */
    visibility: hidden;
    opacity: 0;
    width: 150px;
    height: 25px;
    background-color: rgb(219, 219, 219);
    color: rgb(195, 56, 56);
    text-align: center;
    /* float: right; */
    border-radius: 5px;
    /* padding: 0px 3px; */
    transition: opacity 1s ease-in-out;
    position: absolute;
    z-index: 1;
    /* left: 38em; */
    margin-bottom: 0em;
  }
  
  .adobeillu:hover:before {
    opacity: 1;
    visibility: visible;
  }
  
